import React from "react";
import Room from "./Room/Room";
import { Row, Button } from "react-bootstrap";
import createSession from "../../../controllers/createSession";

class RoomsList extends React.Component {
  state = {
    rooms: [
      {
        session_id: 1,
        users: [
          {
            user_id: 13,
            token: 123
          }
        ]
      },
      {
        session_id: 2,
        users: [
          {
            user_id: 13,
            token: 123
          }
        ]
      }
    ]
  };
  getSession = () => {
    fetch(`http://localhost:5000/api/rooms/`)
      .then(res => res.json())
      .then(res => {
        if (res.data.length === 0) {
          let session_id = new Promise(resolve => {
            let result = createSession();
            resolve(result);
          });
          session_id.then(res => {
            this.setState({ session_id: res });
            this.addSessionId(this.state.session_id);
          });
        } else {
          this.setState({ sessions: res, session_id: res.data[0].session_id });
          return res;
        }
      })
      .catch(err => console.log(err));
  };
  render() {
    return (
      <React.Fragment>
        <Button variant="primary" className="ml-5" onClick={this.getSession}>
          Create Room
        </Button>
        <Row className="justify-content-between">
          {this.props.rooms.map((room, index) => {
            return <Room key={index} room={room} />;
          })}
        </Row>
      </React.Fragment>
    );
  }
}

export default RoomsList;
